	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"This talk is part of Cerner's Tech Talk series. Check us out at http://engineering.cerner.com/ and @CernerEng\n\nAt most companies, advanced analytics expertise is contained in a lab environment: a small team of analysts sitting at their computers and churning out reports and insights to support business decisions. But the real impact from advanced analytics comes from building models that make real-time decisions within production workflows. We will discuss how to use the ecosystem of technologies around Hadoop to support bringing models out of the lab and into the factory, with a focus on strategies for data integration, large-scale machine learning, and experimentation.\n\nAbout the Speaker:\nJosh Wills (@josh_wills) is the Senior Director of Data Science at Cloudera, a leading distrubtor of Hadoop and related services. Wills is the creator of Apache Crunch (a top level Apache project) and serves as the project's chair. Prior to Cloudera, Wills was an engineer at Google. Wills is an open source advocate and all around awesome guy.","meta":{"duration":"PT1H4M28S"},"presenter":{"name":null},"source":{"id":"v-91JycaKjc","provider":"youtube"},"slug":"from-the-lab-to-the-factory-building-a-production-machine-learning-infrastructure","tag":["Cerner (Organization)","tech talk","Machine Learning (Field Of Study)","Apache Software Foundation (Organization)","programming","Statistics (Field Of Study)","Apache Hadoop (Software)","apache crunch"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/v-91JycaKjc/default.jpg","height":90,"width":120}},"title":"From the Lab to the Factory: Building a Production Machine Learning Infrastructure"}}]}},"pathContext":{"slug":"machine-learning-field-of-study","tag":"Machine Learning (Field Of Study)"}}

